              <g transform="translate(50,270)">
                <line stroke="currentColor" x2="100%"></line>
              </g>
              <text x="0" y="230">10</text>
              <g transform="translate(50,230)">
                <line stroke="currentColor" x2="100%"></line>
              </g>
              <text x="0" y="190">20</text>
              <g transform="translate(50,190)">
                <line stroke="currentColor" x2="100%"></line>
              </g>
              <text x="0" y="150">30</text>
              <g transform="translate(50,150)">
                <line stroke="currentColor" x2="100%"></line>
              </g>
              <text x="0" y="110">40</text>
              <g transform="translate(50,110)">
                <line stroke="currentColor" x2="100%"></line>
              </g>
              <text x="0" y="70">50</text>
              <g transform="translate(50,70)">
                <line stroke="currentColor" x2="100%"></line>
              </g>
              <text x="0" y="30">60</text>
              <g transform="translate(50,30)">
                <line stroke="currentColor" x2="100%"></line>
              </g>
            </g>
            <!--  横の目盛り  -->
            <g>
              <b-link><text x="80" y="290">担当者01</text></b-link>
              <b-link><text x="160" y="290">担当者02</text></b-link>
              <b-link><text x="240" y="290">担当者03</text></b-link>
              <b-link><text x="320" y="290">担当者04</text></b-link>
              <b-link><text x="400" y="290">担当者05</text></b-link>
              <b-link><text x="480" y="290">担当者06</text></b-link>
              <b-link><text x="560" y="290">担当者07</text></b-link>
              <b-link><text x="640" y="290">担当者08</text></b-link>
              <b-link><text x="720" y="290">担当者09</text></b-link>
              <b-link><text x="800" y="290">担当者10</text></b-link>
              <b-link><text x="880" y="290">担当者11</text></b-link>
              <b-link><text x="960" y="290">担当者12</text></b-link>
            </g>
            <g fill="#062854">
              <text
                v-for="data in dataSet"
                :key="data.person + '_text'"
                :x="data.person * 80 + 10"
                :y="270 - data.val * 4.5"
                width="40"
              >
                {{ data.val }}
              </text>
              <rect
                v-for="data in dataSet"
                :key="data.person"
                :x="data.person * 80"
                :y="270 - data.val * 4"
                :height="data.val * 4"
                width="40"
              />
            </g>
          </svg>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col cols="12" xl="1"></b-col>
        <b-col cols="12" xl="2">
          <b-link
            @click="gotoPersonContractList"
            href="javascript:;"
            class="card-link link"
            ><u>{{ $t("SHOWLIST") }}</u></b-link
          >
        </b-col>
        <b-col cols="12" xl="5">
          <b-pagination
            id="message-list"
            v-if="lists.length != 0"
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            align="center"
          ></b-pagination>
        </b-col>
      </b-row>
      <br />
    </div>
    <b-modal
      id="supplier-modal"
      ref="supplier-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("SUPPLIERSELECT") }}
        </div>
        <b-button
          @click="cancel"
          class="back-btn close-btn mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <b-form-group>
              <b-form-radio-group
                id="radio-group-1"
                v-model="supplierSelect"
                :options="supplierOptions"
                class="radioControl mb-3 pt-2"
                size="md"
                stacked
              ></b-form-radio-group>
            </b-form-group>
          </b-col>
        </b-row>
      </b-card>
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button @click="getSupplier" variant="primary" class="float-right"
            >{{ $t("SELECTEXPENSEITEMS") }}
          </b-button>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dateFrom: "",
      dateTo: "",
      formatted: "",
      selected: "2",
      referenceFlagOptions: [
        { value: 1, text: "サプライヤー別契約割合" },
        { value: 2, text: "費目別契約割合" },
        { value: 3, text: "担当別契約獲得数" },
      ],
      supplierSelect: {},
      supplierOptions: [
        { value: "1", text: "サプライヤーA" },
        { value: "2", text: "サプライヤーB" },
        { value: "3", text: "サプライヤーC" },
      ],
      tableData: [
        {
          key: "selectRow",
          label: "",
          class: "text-center normal-width",
        },
      ],

      selectMode: "single",
      currentPage: 1,
      perPage: 2,
      // 棒グラフ
      dataSet: [
        { person: 1, val: 50 },
        { person: 2, val: 45 },
        { person: 3, val: 40 },
        { person: 4, val: 35 },
        { person: 5, val: 30 },
        { person: 6, val: 29 },
        { person: 7, val: 35 },
        { person: 8, val: 27 },
        { person: 9, val: 26 },
        { person: 10, val: 25 },
        { person: 11, val: 24 },
        { person: 12, val: 45 },
      ],
    };
  },
  computed: {
    rows() {
      return this.tableData.length;
    },
    lists() {
      const items = this.tableData;
      return items.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },
  },
  mounted() {},

  methods: {
    showSupplierSelect() {
      this.supplierSelect = "";
      this.$refs["supplier-modal"].show();
    },
    getSupplier() {
      this.$refs["supplier-modal"].hide();
    },
    onContext(ctx) {
      this.formatted = ctx.selectedFormatted;
      this.selected = ctx.selectedYMD;
    },
    gotoSupplierContractList() {
      this.$router.push({ name: "supplier_contract_list" });
    },
    gotoCostItemContractList() {
      this.$router.push({ name: "cost_item_wise_contract_list" });
    },
    gotoPersonContractList() {
      this.$router.push({ name: "person_wise_contract_list" });
    },
  },
};
</script>

<style>
.hrBarType {
  height: 2px;
  border-width: 10;
  color: gray;
  background-color: gray;
}

.required-btnAny {
  padding: 2px 5px 2px 5px !important;
  margin-left: 10px;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
}
.form-control {
  border-radius: inherit;
  height: 35px !important;
}

.svg {
  background: rgba(0, 0, 0, 0);
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  border-radius: 50%;
}
</style>